.container--pokemon{
    margin: auto;
    margin-top: 25%;
}
.container--pokemon .pokemons{
    width: 317px;
    height: 170px;
    margin: auto;
}
.container--pokemon img{
    margin-bottom: 1.3rem;
}
.pokemon--types{
    display: flex;
    align-items: center;
    justify-content: center;
}
.pokemon--types .type{
    width: 150px;
    margin: .5rem;
    padding: 5px;
    text-align: center;
    border: 1px solid #000;
    border-radius: 20px;
}
.container--dark--mode div p{
    color: var(--tertiary);
}
.container--pokemon--attributes {
    display: flex;
    justify-content: center;
    margin-top: .5rem; 
    background: var(--tertiary);
    text-align: center;
    padding-bottom: 5rem;
    border-radius:  1rem 1rem 0 0 ;
}
.pokemon--attributes{
    padding-bottom: 23%;
    width: 50%;
    display: flex;
    margin: 1.5rem;
    justify-content: space-between;
}
.pokemon--attributes .attribute p{
    margin-bottom: 10px;
    font-family: 'Press Start 2P';
    font-size: 1rem;
    color: var(--quaternary);
}
.pokemon--attributes .attribute span{
    color: var(--secondary);
}
.description_pokemons, .description_charmander, .description_ivysaur, .description_charmeleon, .description_venusaur, .description_charizard{
    background: transparent;
}
.container--pokemon .description_charmander{
    left: 20%;
}
.checkbox__darkmode{
    height: 100vh;
}

.darkmode{
    display: flex;
    justify-content: center;
    height: 100vh;
    margin-top: 34%;
}

.container--settings{
    margin: 10rem 5rem;
    display: flex;
    text-align: center;
    justify-content: center;
    flex-direction: column;
}
.container--dark--mode{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.slider .round {
    border-radius: 34px;
}   

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    transition: .4s;
}  

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
  }
  
  .switch input {
    display: none;
  }
  
  .slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: var(--tertiary);
    transition: .4s;
  }
  
  .slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    transition: .4s;
  }
  
  input:checked + .slider {
    background-color: var(--tertiary);
  }
  
  input:focus + .slider {
    box-shadow: 0 0 1px var(--tertiary);
  }
  
  input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    transform: translateX(26px);
  }
  
  .slider.round {
    border: 1px solid var(--tertiary);
    border-radius: 34px;
    background: var(--quaternary);
  }
  
  .slider.round:before {
    border-radius: 50%;
  }
  
*{
    padding: 0;
    margin: 0; 
    box-sizing: border-box;
    font-size: 0.8rem;
    font-family: 'Press Start 2P';
}
:root{
    --primary: #F2250A;
    --secondary: #20CBB8;
    --tertiary: #24272E;
    --quaternary: #D6DBDE;
    --quinquennial: #81BAC3;
}
